<template>
  <div class="searchresult">
    <el-image :style="{width:'100%'}" :src="style.src_pic"></el-image>
    <div class="main_module_body" v-show="currentLang == 'zh'">
      <!-- <el-row :style="ListBackground" :gutter="0"> -->
      <el-row :gutter="0">
        <el-col :span="2">
          <p>&nbsp;</p>
        </el-col>
        <el-col class="modulelist" :span="20">
          <p class="resultinfo">
            共搜索到符合条件的女佣共
            <span class="resultnum">6</span>
            名
          </p>
          <!-- <el-checkbox-group> -->
          <div
            class="module_content"
            v-for="(item,index) in module.slice(0,3)"
            :key="index + 'aasda' "
          >
            <div class="module_info">
              <!--  -->
              <el-row :gutter="0">
                <el-col :span="8">
                  <div class="avatar">
                    <img :style="{width:'120px',height:'200px'}" :src="item.avatar">
                  </div>
                </el-col>
                <el-col :span="2">
                  <p>&nbsp;</p>
                </el-col>
                <el-col :span="14">
                  <!--  -->
                  <el-row :gutter="0">
                    <el-col :span="14">
                      <p class="module_product_name">{{item.Name}}</p>
                    </el-col>
                    <el-col class="submitoption" :span="5">
                      <el-checkbox :disabled="false" label></el-checkbox>
                      <!-- <el-checkbox  label></el-checkbox> -->
                    </el-col>
                    <el-col class="starP" :span="5">
                      <!-- <span class="star">★</span> -->
                      <span
                        v-show="item.IfCollect"
                        @click="ifStar(item.ID)"
                        :style="{color: style.starColorOK}"
                        class="star"
                      >★</span>
                      <span
                        v-show="!item.IfCollect"
                        @click="ifStar(item.ID)"
                        :style="{color: style.starColorNO}"
                        class="star"
                      >★</span>
                    </el-col>
                  </el-row>
                  <!--  -->
                  <p>性别：{{item.Sex}}</p>
                  <p>年龄：{{item.Age}}</p>
                  <p>婚否：{{item.MaritalStatus}}</p>
                  <p>经验：{{item.WorkExp}}</p>
                  <p>国籍：{{item.Nationality}}</p>
                  <p>
                    海外工作经验：
                    <span
                      v-for="(itemChilren,index) in item.OverseasWorkplace"
                      :key="index + 'c' "
                    >
                      <span>{{itemChilren}}&nbsp;&nbsp;</span>
                    </span>
                  </p>
                  <p>
                    女佣星级：
                    <span
                      v-for="(itemChildren,index) in item.TheMaidStar"
                      :key="index + 'aasda' "
                    >
                      <span class="level">★</span>
                    </span>
                  </p>
                </el-col>
              </el-row>
              <!--  -->
            </div>
          </div>
          <!-- </el-checkbox-group> -->
          <!--  -->
          <div
            class="module_content"
            v-for="(item2,index2) in module.slice(3,6)"
            :key="index2 + 'aasda2' "
          >
            <div class="module_info">
              <!--  -->
              <el-row :gutter="0">
                <el-col :span="8">
                  <div class="avatar">
                    <img :style="{width:'120px',height:'200px'}" :src="item2.avatar">
                  </div>
                </el-col>
                <el-col :span="2">
                  <p>&nbsp;</p>
                </el-col>
                <el-col :span="14">
                  <!--  -->
                  <el-row :gutter="0">
                    <el-col :span="14">
                      <p class="module_product_name">{{item2.Name}}</p>
                    </el-col>
                    <el-col class="submitoption" :span="5">
                      <el-checkbox :disabled="false" label></el-checkbox>
                      <!-- <el-checkbox  label></el-checkbox> -->
                    </el-col>
                    <el-col class="starP" :span="5">
                      <!-- <span class="star">★</span> -->
                      <span
                        v-show="item2.IfCollect"
                        @click="ifStar(item2.ID)"
                        :style="{color: style.starColorOK}"
                        class="star"
                      >★</span>
                      <span
                        v-show="!item2.IfCollect"
                        @click="ifStar(item2.ID)"
                        :style="{color: style.starColorNO}"
                        class="star"
                      >★</span>
                    </el-col>
                  </el-row>
                  <!--  -->
                  <p>性别：{{item2.Sex}}</p>
                  <p>年龄：{{item2.Age}}</p>
                  <p>婚否：{{item2.MaritalStatus}}</p>
                  <p>经验：{{item2.WorkExp}}</p>
                  <p>国籍：{{item2.Nationality}}</p>
                  <p>
                    海外工作经验：
                    <span
                      v-for="(itemChilren,index) in item2.OverseasWorkplace"
                      :key="index + 'c' "
                    >
                      <span>{{itemChilren}}&nbsp;&nbsp;</span>
                    </span>
                  </p>
                  <p>
                    女佣星级：
                    <span
                      v-for="(itemChildren,index) in item2.TheMaidStar"
                      :key="index + 'aasda' "
                    >
                      <span class="level">★</span>
                    </span>
                  </p>
                </el-col>
              </el-row>
              <!--  -->
            </div>
          </div>
        </el-col>
        <el-col :span="2">
          <p>&nbsp;</p>
        </el-col>
      </el-row>
    </div>
    <el-row class="matching">
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="18">
        <el-button size="medium" type="primary" class="matching_btn">提交</el-button>
        <el-button size="medium" class="reset_btn" @click="resetData()">重置</el-button>
      </el-col>
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
    </el-row>
    <div class="submitinfoP">
      <p class="submitinfo">（提交选中的菲佣，我们的客服会在1-3个工作日内和您取得联系，请保持注册手机的畅通，谢谢！）</p>
    </div>
  </div>
</template>


<script>
import { mapGetters } from "vuex";

export default {
  name: "SearchResult",
  computed: {
    ...mapGetters(["currentLang", "module"])
  },
  data() {
    return {
      ListBackground: {
        backgroundImage: "url(" + require("@/assets/newpic11.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100%"
      },
      style: {
        src_pic: require("@/assets/maid/pic (2).png"),
        starColorOK: "#3081FD",
        starColorNO: "#EDF0F9"
        // starColor
      }
    };
  },
  methods: {
    resetData() {},
    ifStar(ID) {
      console.log("~~~~ifStar");
      let vm = this;
      vm.$store
        .dispatch("update_ifCollectByID", ID)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
    }
  }
};
</script>

<style>
.searchresult {
  width: 100%;
  max-width: 2000px;
  min-width: 1200px;
  background: #ffffff;
  padding: 60px 0px;
}
.searchresult .resultinfo {
  margin-left: 20px;
  margin-top: 45px;
  color: #222222;
  font-size: 22px;
}

.searchresult .resultnum {
  color: #ff0000;
  font-size: 22px;
}

.searchresult .main_module_body {
  width: 1440px;
  margin: 0 auto;
  overflow: hidden;
  margin-bottom: 20px;
}
.searchresult .module_content {
  width: 30%;
  /* margin: 50px 40px 0px 50px; */
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  float: left;
  background: #fff;
}

.searchresult .module_product_name {
  font-size: 22px;
  margin: 10px 0px;
}

.searchresult .el-checkbox__inner {
  margin-top: 15px;
}

.searchresult .starP > span {
  margin-top: 7px;
}

.searchresult .star {
  font-size: 20px;
  color: #3081fd;
  display: inline-block;
}

.searchresult .level {
  font-size: 20px;
  color: #ffc618;
  display: inline-block;
}
/* ----new */

.searchresult .module_content .avatar {
  text-align: center;
  /* margin-top: 30px; */
}
.searchresult .module_content .avatar > img {
}
.searchresult .module_info {
  /* margin-left: 25px; */
  padding: 15px;
  border: 1px solid #dadada;
}
.searchresult .module_info p {
  margin: 5px 0px;
}

.searchresult .matching {
  height: 120px;
  text-align: center;
}
.searchresult .matching:nth-child(n) {
  margin-top: 50px;
}
.searchresult .matching .matching_btn {
  margin-right: 200px;
}
.searchresult .matching .reset_btn,
.searchresult .matching .matching_btn {
  width: 180px;
  height: 45px;
  font-size: 16px;
}
.searchresult .submitinfoP {
  text-align: center;
}
.searchresult .submitinfoP .submitinfo {
  color: #a6a6a6;
  font-size: 16px;
}
</style>

<style scoped>
</style>



